const AutumnQ = () => {
  return (
    <div style={card("700px")}>
      <Paper style={paperstyle}>
      <div> <h3>Honors 100 - Autumn Quarter Reflection</h3>  <h4> Fall 2019 </h4> 
          <p> At the start of autumn quarter, I was thinking about double majoring in potentially neuroscience alongside computer science. Now, Im not so sure about doing two science majors. Talking to alumni, Ive found that there are lots of different ways to get involved with brain science labs without being a neuroscience major. In fact, for some of these labs, 
          simply knowing the programming language Java is enough to make you a valuable part of the team. This lifted a lot of weight off my shoulders and Im going to spend my time exploring classes that exercise my creative side. One thing that surprised me, that I did not expect from UW was how welcoming everyone is. Once you put yourself out there, making friends isnt so intimidating. This is one thing I really like about the Honors community and that Honors 100 has helped me with: finding friends. Im excited to create more memories and connections with my peers and build up my portfolio in this way. In Honors 100, Ive been exposed to the global challenge posed by tech within society. This is definitely something I want 
          to explore more. The global challenges event was very interesting for me because I got to hear about the social justice side of the issue. However, in my Honors CSE 390 class, I got to learn more about the technical side. My CSE 390 teacher, Hunter Schafer, does a great job of explaining how machine learning can lead to racial biases and dangerous feedback loops. I want to dedicate more of my personal time to investigating this topic. Thus, the introduction from Honors 100 really helped with giving me context for this issue before I jumped into the technicality of it with Hunter. By the end of my first year in Honors, I hope to see better how my major 
          intersects with different aspects of my life. First, I saw its risks come to life at the Global Challenges event. Now, I want to see ways we can use technology to combat its own dangers. How can it be used in an interdisciplinary way? What are its further applications? For winter quarter, I am taking the honors class Ways of Feeling, and Im hoping to understand emotions on a deeper level in this class. Then, I could even apply technology to the contents of this class. This way, Honors will have taught me how to see the overlap between different subjects. By the end of my time at UW, I really hope I have explored all the different subjects I have come in curious about. I know I will never get to know everything about everything, but at least at university I have the opportunity to learn as much as I can. Its kind of like an all-you-can-eat buffet, but theres just so much food that will never be finished. From art and animation, to astronomy, to neuroscience, from linguistics to psychology, film, law, and 
          economics, there is just so much I want to taste. It's unquenchable thirst but with the help with the Honors interdisciplinary approach, Id like to say I lived my university life to its fullest during these next four years.</p></div>
          </Paper>
    </div>
  );
};

